
.border-df {border:1px solid black;}
.border-radius-df {border-radius: 8px;}

.header { 
    button {padding:5px 8px; @extend .border-radius-df; margin:0 5px; 
        &:hover {opacity: 1;}
        a {color:#000;}
    }
}
.footer { 
    button {padding:5px 8px; border:0; @extend .border-radius-df; margin:0 5px; opacity: .8;
        &:hover {opacity:1; transition:.3s;}
    }
}

.gr__headline, .gr__content {height:43vh;}
.gr__credential-i { display:grid; grid-template-columns:auto auto; gap:10px;
    div { padding:10px 25px; background:var(--form-blue); 
        label {padding-right:5px;}
        select {width:25%;}
        div { padding:8px 0; border: 1px solid var(--butt-green); margin: 5px auto 0 auto; }
    }
}
.gr__insert {width:25vw; border:1px solid var(--butt-green); @extend .border-radius-df;
    form {
        label {width:32%; display:inline-block;}
        input {width:65%;}
        button {padding:8px 12px; display:block; margin:5px auto 0 auto; background: var(--bg-second); color:#fff; @extend .border-radius-df;}
    }
}
.gr__insert-display {width:74vw;}

.gr__list { &:hover {background:whitesmoke;}
    div {border-top:1px solid var(--tr-grey); border-bottom:1px solid var(--tr-grey); 
        
    }
    div:nth-child(1) {width:3%; border-left:1px solid var(--tr-grey); border-right:1px solid var(--tr-grey);}
    div:nth-child(2) {width:15%; border-right:1px solid var(--tr-grey);}
    div:nth-child(3) {width:20%; border-right:1px solid var(--tr-grey);}
    div:nth-child(4) {width:7%; border-right:1px solid var(--tr-grey);}
    div:nth-child(5) {width:7%; border-right:1px solid var(--tr-grey);}
    div:nth-child(6) {width:7%; border-right:1px solid var(--tr-grey);}
    div:nth-child(7) {width:10%; border-right:1px solid var(--tr-grey);}
    div:nth-child(8) {width:10%; border-right:1px solid var(--tr-grey);}
    div:nth-child(9) {width:10%; border-right:1px solid var(--tr-grey);}

    button { border-radius:4px; padding:3px 7px;}
}
.gr__total {position: absolute; bottom:2vh; right:65vh;}

.history__list {
    div {border:1px solid var(--tr-grey);}
}
.history__list-option {border:1px solid var(--tr-grey); border-radius:8px;}
.history__result {height:10vh;}

.modal__box { 
    //display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */

    div {background-color: #fefefe; @extend .border-radius-df;
        margin: auto;
        padding: 20px;
        border: 1px solid #888;
        width: 80%;
    }
  }

@media only screen and (min-width:1919px) and (min-height:1079px) {
    .history__result {height:30vh;}

}